import { MouseEvent, forwardRef } from "react"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { ArrowDropDown } from "@rp/react/web/mui/icons"
import { COUNTER_NAME } from "./InlineAttribution"

type Props = { onClick: (ev: MouseEvent) => void; disabled?: boolean; isModalOpen: boolean }
const InlineAttributionNumberedButton = forwardRef<HTMLDivElement, Props>(
  ({ onClick, disabled, isModalOpen, ...rest }, ref) => {
    const { isMobile } = useDeviceInfo()

    const disabledColor = disabled ? "text.disabled" : "primary.main"
    const disabledBgColor = disabled ? "overlay.surface" : "blue.900"

    return (
      <Box
        {...rest}
        component="span"
        onClick={!disabled ? onClick : undefined}
        ref={ref}
        sx={{
          display: "inline-block",
          position: "relative",
          height: isMobile ? 20 : 16,
          width: isMobile ? 20 : 16,
          mb: isMobile ? 0 : 0.25,
          mx: 0.25,
          cursor: isMobile || disabled ? undefined : "pointer",
          "&:hover::after, &[aria-labelledby]::after": !disabled
            ? {
                backgroundColor: "primary.main",
                color: "surface.main",
                transform: "rotateZ(0deg)",
              }
            : undefined,
          "&::after": {
            color: isModalOpen ? "surface.main" : disabledColor,
            backgroundColor: isModalOpen ? "primary.main" : disabledBgColor,
            borderRadius: "3px",
            position: "absolute",
            height: isMobile ? 20 : 16,
            width: isMobile ? 20 : 16,
            top: 4,
            left: 0,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            fontSize: 8,
            counterIncrement: COUNTER_NAME,
            content: `counter(${COUNTER_NAME})`,
          },
        }}
      />
    )
  },
)

const InlineAttributionArrowButton = forwardRef<HTMLDivElement, Props>(
  ({ onClick, disabled = false, isModalOpen, ...rest }, ref) => {
    const { isMobile } = useDeviceInfo()

    const disabledColor = disabled ? "text.disabled" : "primary.main"
    const disabledBgColor = disabled ? "overlay.surface" : "blue.900"

    return (
      <Box
        {...rest}
        role="button"
        aria-label="Inline attribution button"
        component="span"
        ref={ref}
        onClick={!disabled ? onClick : undefined}
        sx={{
          display: "inline-block",
          position: "relative",
          height: isMobile ? 20 : 16,
          width: isMobile ? 20 : 16,
          mb: isMobile ? 0 : 0.25,
          mx: 0.25,
          cursor: isMobile || disabled ? undefined : "pointer",
          "& svg": {
            transition: "transform 0.2s",
            fontSize: isMobile ? 18 : 14,
          },

          "&:hover > div": {
            backgroundColor: "primary.main",
            color: "surface.main",
          },
          "&:hover svg": !disabled
            ? {
                color: "surface.main",
                transform: "rotateZ(180deg)",
              }
            : undefined,
        }}
      >
        <Box
          component="span"
          sx={{
            color: isModalOpen ? "surface.main" : disabledColor,
            backgroundColor: isModalOpen ? "primary.main" : disabledBgColor,
            borderRadius: "3px",
            position: "absolute",
            height: isMobile ? 20 : 16,
            width: isMobile ? 20 : 16,
            top: 4,
            left: 0,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <ArrowDropDown />
        </Box>
      </Box>
    )
  },
)

export { InlineAttributionNumberedButton, InlineAttributionArrowButton }
